import { Suspense, lazy } from 'react';
import _ from 'lodash';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import { BurgerMenu } from './components/burger-menu';
import { ErrorBoundary } from 'react-error-boundary';



const RouteHome = lazy(() => import('./routes/home'));
const RouteCards = lazy(() => import('./routes/cards'));
const RouteCircle = lazy(() => import('./routes/circle'));
const RouteEyes = lazy(() => import('./routes/eyes'));



function App() {
	return (
		<div className='App'>
			<BrowserRouter>
				<BurgerMenu items={[
					{title: 'home', path: '/'},
					{title: 'cards', path: '/cards'},
					{title: 'circle', path: '/circle'},
					{title: 'eyes', path: '/eyes'},
				]} />
				<Suspense fallback={<div style={{
					position: 'absolute',
					height: '100%',
					width: '100%',
					backgroundColor: 'black',
					color: 'white',
					opacity: 0.5,
					display: 'flex',
					justifyContent: 'center',
					alignItems: 'center',
				}}>
					<div>loading...</div>
				</div>}>
					<Routes>
						<Route path='/' element={<RouteHome />} />
						<Route path='/cards' element={<RouteCards />} />
						<Route path='/circle' element={<RouteCircle />} />
						<Route path='/eyes' element={<RouteEyes />} />
					</Routes>
				</Suspense>
			</BrowserRouter>
		</div>
	);
}

export default App;
