import { useState } from 'react';
import { useLocation, useNavigate } from 'react-router-dom';
import _ from 'lodash';
import { useGesture } from '@use-gesture/react';
import { RxHamburgerMenu } from 'react-icons/rx';
import './style.css';



export interface IBurgerMenuItem
{
    title: string,
    path: string,
}

export interface IBurgerMenuProps
{
    items: IBurgerMenuItem[],
}

export function BurgerMenu(props:IBurgerMenuProps)
{
    const {
        items,
    } = props;

    const navigate = useNavigate();
    const location = useLocation();

    const [isExpanded, setIsExpended] = useState(false);
    
    const bindGestures = useGesture({
        onMouseEnter: () => setIsExpended(true),
        onMouseLeave: () => setIsExpended(false),
    });

    const classNames = (
        _.chain([
            'burger-menu',
            isExpanded ? 'expanded' : undefined,
        ])
        .compact()
        .join(' ')
        .value()
    );

    return <div className={classNames} {...bindGestures()}>
        <div className='button'>
            <RxHamburgerMenu />
        </div>
        <div className='items'>
            {items.map(({title, path}, i) => (
                <div
                    key={i}
                    className={location.pathname === path ? 'item active' : 'item'}
                    onClick={() => navigate(path)}
                >
                    {title}
                </div>
            ))}
        </div>
    </div>
}